@charset "UTF-8";
@import url(http://fonts.googleapis.com/earlyaccess/notosansjapanese.css);
html {
  font-size: 62.5%;
}

body {
  font-family: "Noto Sans Japanese", 游ゴシック体, "Yu Gothic", YuGothic, "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", メイリオ, Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  background: url("/img/free5.jpeg") no-repeat fixed left bottom;
  background-size: cover;
  font-weight: 200;
}

a {
  color: #fff;
}

ul {
  margin: 0;
}

.background {
  height: 100vh;
  background: linear-gradient(to bottom right, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.8)) fixed;
  color: #fff;
  margin-bottom: 0;
}

.divider {
  background-color: #fff;
}

/*
  メインコンテンツ
 */
.main-content {
  font-size: 4rem;
  padding: 2vh !important;
  height: 100vh;
  border: solid 1px #fff;
}

.main-content .category-box {
  height: 10%;
}

.main-content .category-box .category-name, .main-content .category-box .category-actions {
  display: inline-block;
}

.main-content .category-box .category-name {
  margin-right: 10px;
}

.main-content .category-box .equipment-actions-list li {
  font-size: 0.7em;
  border: solid 1px #fff;
}

.main-content .category-box .equipment-actions-list li:hover {
  background: rgba(255, 255, 255, 0.3);
}

.main-content .detail-box {
  height: 90%;
}

.main-content .detail-box .status-area {
  height: 100%;
}

.main-content .detail-box .status-area .player-status-list {
  font-size: 2.5rem;
  text-align: right;
}

.main-content .detail-box .status-area .player-status-list span {
  float: left;
  opacity: .8;
}

.main-content .detail-box .status-area .left-area {
  float: left;
  width: 50%;
  padding: 10px;
  border-right: solid 1px #fff;
}

.main-content .detail-box .status-area .right-area {
  float: right;
  width: 50%;
  padding: 10px;
}

.main-content .detail-box .skills-area {
  height: 100%;
}

.main-content .detail-box .equipment-area {
  height: 100%;
}

.main-content .detail-box .equipment-area .equipment-list {
  font-size: 2.5rem;
}

.main-content .detail-box .equipment-area .equipment-list li {
  padding: 5px 0;
}

.main-content .detail-box .equipment-area .equipment-list li:hover {
  background: rgba(255, 255, 255, 0.3);
}

.main-content .detail-box .title-area {
  height: 100%;
  position: relative;
}

.main-content .detail-box .title-area .title-list {
  height: 100%;
  overflow: auto;
  font-size: 2rem;
  padding: 0 10px;
}

.main-content .detail-box .title-area .title-list li {
  padding: 5px 0;
}

.main-content .detail-box .title-area .title-list li:hover {
  background: rgba(255, 255, 255, 0.3);
}

.main-content .detail-box .title-area .left-area {
  width: 50%;
  height: 80%;
  float: left;
  padding: 10px;
  border-right: solid 1px #fff;
}

.main-content .detail-box .title-area .right-area {
  height: 80%;
  padding: 10px;
}

.main-content .detail-box .title-area .bottom-area {
  position: absolute;
  top: 80%;
  width: 100%;
  height: 20%;
  border: solid 1px #fff;
}

/*
  サイドコンテンツ
 */
.side-content {
  height: 100vh;
  border: solid 1px #fff;
  font-size: 3em;
  padding: 20px !important;
}

.side-content ul.category-list li {
  margin-bottom: 10px;
}

.side-content ul.category-list li:hover {
  background: rgba(255, 255, 255, 0.3);
}

.side-content ul.category-list a {
  display: block;
}

.avatar {
  width: 10%;
  object-fit: cover;
}

.btn.ghost {
  border: solid 1px #fff;
  background: rgba(0, 0, 0, 0.1);
}

.vertical-divider {
  width: 1px;
  height: 100%;
  background-color: #fff;
}

.text-area {
  height: 100vh;
  position: relative;
}

.text-area .text-box {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 2rem;
  width: 100%;
  height: 50%;
  position: absolute;
  top: 25%;
  bottom: 0;
  margin: 0;
  text-align: center;
  overflow-y: auto;
}

.text-area .text-box .text-element {
  font-size: 2.8rem;
  letter-spacing: 2px;
}

.text-area .text-box .text-element .open-door, .text-area .text-box .text-element .close-door {
  width: 45%;
  background-color: rgba(0, 0, 0, 0.1);
  border: solid 1px #fff;
}

.text-area .text-box .text-element .open-door:hover, .text-area .text-box .text-element .close-door:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

.text-area .text-box .text-element .open-door {
  margin-right: 1rem;
}

.typed-cursor {
  opacity: 1;
  -webkit-animation: blink 0.7s infinite;
  -moz-animation: blink 0.7s infinite;
  animation: blink 0.7s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
